import AppBar from '@material-ui/core/AppBar';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Toolbar from '@material-ui/core/Toolbar';
import SearchIcon from '@material-ui/icons/Search';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import { useHistory } from "react-router-dom";
import { axiosWithAuth } from '../utils/axiosWithAuth';
import VentilatorTable from './tables/VentilatorTable';


const styles = (theme) => ({
    paper: {
        maxWidth: 1100,
        margin: 'auto',
        overflow: 'hidden',
    },
    searchBar: {
        borderBottom: '1px solid rgba(0, 0, 0, 0.12)',
    },
    searchInput: {
        fontSize: theme.typography.fontSize,
    },
    block: {
        display: 'block',
    },
    addUser: {
        marginRight: theme.spacing(1),
    },
    contentWrapper: {
        margin: '40px 16px',
    },
    load: {
        width: '100%',
        '& > * + *': {
            marginTop: theme.spacing(2),
        },
    },
    // no_vents: {
    //     display: "flex",
    //     justifyContent: "center",
    //     width: "100%",
    //     height: "20vh"
    // }
});


function Content(props) {
    const { classes } = props;

    let history = useHistory();

    const [ventilators, setVentilators] = useState([])

    const id = localStorage.getItem("id");

    useEffect(() => {
        axiosWithAuth()
            .get(`/users/${id}/vents`)
            .then(res => {
                setVentilators(res.data)
            })
            .catch(err => console.log(err))

    }, )

    return (
        <Paper className={classes.paper}>

            <AppBar className={classes.searchBar} position="static" color="default" elevation={0}>
                <Toolbar>
                    <Grid container spacing={2} alignItems="center">
                        <Grid item>
                            <SearchIcon className={classes.block} color="inherit" />
                        </Grid>
                        <Grid item xs>
                            <TextField
                                fullWidth
                                placeholder="Search by brand or model"
                                InputProps={{
                                    disableUnderline: true,
                                    className: classes.searchInput,
                                }}
                            />
                        </Grid>
                        <Grid item>
                            <Button variant="contained" color="primary" className={classes.addUser} onClick={() => history.push('/add_vent')}>
                                Add Ventilator
                            </Button>
                        </Grid>
                    </Grid>
                </Toolbar>
            </AppBar>
            {ventilators.length === 0 ? (
                <div className={classes.root}>
                    <div style={{ margin: '20px 0' }}>No Ventilators in Dashboard</div>
                </div>
            ) : (
                    <VentilatorTable vents={ventilators} {...props}/>
                )}
        </Paper>
    );
}

Content.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Content);